<template>
    <div>
        <Head headback='false' bol='密码登录' headto='false' ></Head>
        <div class="main">
            <ul class="login">
                <li><input type="text" v-model="username" placeholder="账号"></li>
                <li>
                    <input type="text" v-if="pas" v-model="password" placeholder="密码">
                    <input type="password" v-if="!pas" v-model="password" placeholder="密码">
                    <van-switch class="switch1" v-model="checked"/>
                </li>
                <li>
                    <input type="text" v-model='proving' placeholder="验证码">
                    <div class="provings">
                        <img :src="provingimg" alt="">
                        <div>
                            <p>看不清</p>
                            <p @click="getpro()">换一张</p>
                        </div>
                    </div>
                </li>
            </ul>
            <div class="remind">
                <p>温馨提示：未注册过的账号，登陆时将自动注册</p>
                <p>注册过的用户可凭账号密码登陆</p>
            </div>
            <button @click="register()">登陆</button>
            <div class="reset"><router-link tag='span' to='/Resetpass'>重置密码？</router-link></div>
            <!-- 弹出框 -->
            <div class="modals" v-if="!warnbol">
                <div class="warn">
                    <span></span>
                    <span></span>
                </div>
                <div class="warn-con">{{warn}}</div>
                <div class="btn" @click="getbtn()">确定</div>
            </div>
        </div>
    </div>
</template>

<script>
import Head from "./../header/Head";
import axios from "axios"
axios.defaults.withCredentials=true
export default {
  components: {
    Head,
  },
  data() {
    return {
      pas: false,
      password: "",
      username: "",
      proving: "",
      checked: false,
      provingimg:"",
      warn:'',
      warnbol:true,
    };
  },
  watch: {
      //监听vant里Switch开关的变换，通过布尔值来切换密码框密码的隐藏和显示
    checked: function() {
      if (this.checked==true) {
        this.pas = true;
      } else {
        this.pas = false;
      }
    }
  },
  methods: {
      //点击换一张切换验证码，通过接口获取验证码
      getpro(){
          var that = this
          this.http.post('http://elm.cangdu.org/v1/captchas',{}).then(res => {
              console.log(res.code)
              if(res.code!=''){
                  this.provingimg=res.code
              }
          })
      },
      //点击登陆，发送账号密码和验证码到后台验证，
      register(){
          //先在本地通过正则验证一下，通过后在发给后台验证
          if(this.username!=''&&this.password!=''&&this.proving!=""){
              var user=/^\w{6,16}$/
              var pass=/^\w{6,16}$/
              if(user.test(this.username)==false){
                  this.warnbol=false
                  this.warn="用户名格式错误"
              }
              if(pass.test(this.password)==false){
                  this.warnbol=false
                  this.warn="密码格式错误"
              }
              if(user.test(this.username)==true&&pass.test(this.password)==true){
                  axios.post('http://elm.cangdu.org/v2/login',{password:this.password,username:this.username,captcha_code:this.proving}).then(res=>{
                      console.log(res)
                      if(res.status==0){
                          if(res.message=="验证码不正确"){
                          this.warnbol=false
                          this.warn="验证码不正确"
                          this.getpro()
                          }
                          if(res.message=="密码错误"){
                          this.warnbol=false
                          this.warn="密码错误"
                          this.getpro()
                         }
                      }else {
                          var obj={}
                          obj.name=this.username
                          obj.pass=this.password
                          obj.avatar=res.avatar;
                          obj.id=res.user_id
                          this.$store.commit('add',obj)
                          this.$router.go(-1)
                      }
                      
                  })
              }
          }
          if(this.username!=''&&this.password==''){
              this.warnbol=false
              this.warn="请输入密码"
              this.getpro()
          }
          if(this.username==''&&this.password!=''){
              this.warnbol=false
              this.warn="请输入手机号/邮箱/用户名"
              this.getpro()
          }
          if(this.username!=''&&this.password!=''&&this.proving==""){
              this.warnbol=false
              this.warn="请输入验证码"
              this.getpro()
          }
          if(this.username==''&&this.password==''&&this.proving=="") {
              this.warnbol=false
              this.warn="请输入手机号/邮箱/用户名"
              this.getpro()
          }
          

      },
      //取消弹出框
      getbtn(){
          this.warnbol=true
      }
  },
  mounted () {
      //把获取验证码的函数放在生命周期里，确保每次刷新都能得到一个新的验证码
      this.getpro()
  }
};
</script>

<style lang="scss" scoped>
.main {
  width: 100%;
  height: 100%;
  padding-top: 0.88rem;
  .login {
    width: 100%;
    height: auto;
    margin-top: 0.28rem;
    background: white;
    li {
      width: 6.94rem;
      height: 0.4rem;
      padding: 0.37rem 0.28rem;
      border-bottom: 0.01rem solid #ccc;
      display: flex;
      input {
        width: 60%;
        height: 0.4rem;
        border: none;
        outline: none;
        display: block;
        font-size: 0.38rem;
      }
      .switch1 {
        margin-left: 2rem;
      }
      .provings{
          width: 40%;
          height: 0.7rem;
          display: flex;
          margin-top: -0.15rem;
          img {
              width: 1.64rem;
              height: 0.7rem;
              margin-right: 0.08rem;
          }
          div {
              width: 0.92rem;
              p {
                  width: 100%;
                  font-size: 0.26rem;
                  margin-bottom: 0.06rem;
              }
              p:nth-child(2){
                  color: #3b95e9;
              }
          }
      }
    }
  }
  .remind{
      width: 6.94rem;
      height: auto;
      margin: 0 auto;
      margin-top: 0.18rem;
      font-size: 0.24rem;
      color: red;
      text-align: left;
      p:nth-child(2){
          margin-top: 0.18rem;
      }
      p {
          margin-bottom: 0.18rem;
      }
  }
  button {
      width: 6.94rem;
      height: 0.9rem;
      margin: 0 auto;
      border: none;
      border-radius: 0.06rem;
      background: #4cd964;
      color: white;
      font-size: 0.4rem;
      margin-bottom: 0.46rem;
  }
  .reset{
      width: 6.94rem;
      height: 0.8rem;
      margin: 0 auto;
      display: flex;
      span {
        color: #3b95e9;
        font-size: 0.34rem;
        margin-left: 5.2rem;
      }
  }
  .modals{
      width: 5.62rem;
      height: 4rem;
      position: fixed;
      left: 0.94rem;top: 0.4rem;
      background: white;
      z-index: 5;
      border-radius: 0.1rem;
      .warn {
          width: 1.4rem;
          height: 1.4rem;
          border-radius: 50%;
          border: 0.07rem solid #f8cb86;
          margin: 0 auto;
          margin-top: 0.28rem;
          span:nth-child(1){
              width: 0.06rem;
              height: 0.7rem;
              background: #f8cb86;
              display: block;
              margin: 0 auto;
              margin-top: 0.2rem;
          }
          span:nth-child(2){
              width: 0.1rem;
              height: 0.1rem;
              border-radius: 50%;
              background: #f8cb86;
              display: block;
              margin: 0 auto;
              margin-top: 0.1rem;
          }
      }
      .warn-con{
          width: 4.26rem;
          height: 0.46rem;
          margin: 0 auto;
          margin-top: 0.38rem;
          font-size: 0.36rem;
      }
      .btn {
          width: 100%;
          height: 0.86rem;
          background: #4cd964;
          margin-top: 0.45rem;
          color: white;
          font-size: 0.46rem;
          line-height: 0.86rem;
          border-bottom-left-radius: 0.1rem;
          border-bottom-right-radius: 0.1rem;
      }
  }
}
</style>



